    padding: 40px 0vw;
  }

  .small-text {
    padding: 0px 6vw;
    font-size: 14px;
    width: 66vw;
    margin: 0;
  }

  .banner {
    /* margin-top: 0vh; */
  }

  .Boat {
    height: 175px;
    width: 175px;
    left: 30%;
